
@charset "UTF-8";
.page:after {
  content: '';
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.page-item {
  display: inline-block;
  vertical-align: middle;
  min-width: 28px;
  height: 28px;
  line-height: 26px;
  margin-right: 0;
  text-align: center;
  list-style: none;
  background-color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  font-family: Arial;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
  transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fcfcfc), to(#ebebeb));
  background-image: linear-gradient(-180deg, #fcfcfc 0%, #ebebeb 100%);
}
.page-item a {
    margin: 0 6px;
    text-decoration: none;
    color: #333;
}
.page-item:hover {
    border-color: #4991fd;
}
.page-item:hover a {
      color: #4991fd;
}
.page-item-active {
    background-color: #4991fd;
    border-color: #4991fd;
    background-image: none;
}
.page-item-active:hover a,
    .page-item-active a {
      color: #fff;
}
.page-item-jump-next:after, .page-item-jump-prev:after {
  content: "•••";
  display: block;
  letter-spacing: 1px;
  color: #ccc;
  text-align: center;
}
.page-item-jump-next i, .page-item-jump-prev i {
  display: none;
}
.page-item-jump-next:hover:after, .page-item-jump-prev:hover:after {
  content: ">>";
  color: #4991fd;
  font-weight: bold;
  font-size: 12px;
}
.page-item-jump-next:hover i, .page-item-jump-prev:hover i {
  display: inline;
}
.page-item-jump-prev:hover:after {
  content: "<<";
}
.page-item-jump-prev:hover i:after {
  content: "\F3D2";
}
.page-item-jump-next:hover i:after {
  content: "\F3D3";
}
.page-item-jump-next, .page-item-jump-prev, .page-next, .page-prev {
  display: inline-block;
  vertical-align: middle;
  min-width: 28px;
  height: 28px;
  line-height: 26px;
  list-style: none;
  text-align: center;
  cursor: pointer;
  color: #666;
  font-family: Arial;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.page-next, .page-prev {
  background-color: #fff;
  padding: 0 5px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fcfcfc), to(#ebebeb));
  background-image: linear-gradient(-180deg, #fcfcfc 0%, #ebebeb 100%);
}
.page-next a, .page-prev a {
    color: #333;
    font-size: 14px;
}
.page-next:hover, .page-prev:hover {
    border-color: #4991fd;
}
.page-next:hover a, .page-prev:hover a {
      color: #4991fd;
}
.page-disabled {
  cursor: not-allowed;
  display: none;
}
.page-disabled a {
    color: #666;
}
.page-disabled:hover {
    border-color: #ccc;
}
.page-disabled:hover a {
      color: #666;
      cursor: not-allowed;
}
.page-options {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}
.page-options-sizer {
    display: inline-block;
    line-height: 28px;
    margin-right: 5px;
}
.page-options-sizer-select {
      display: inline-block;
      position: relative;
}
.page-options-sizer-select:after {
        border: 1px solid #4991fd;
        border-right: 0;
        border-top: 0;
        content: " ";
        display: block;
        height: 7px;
        pointer-events: none;
        position: absolute;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        width: 7px;
        margin-top: -6px;
        right: 14px;
        top: 50%;
}
.page-options-sizer-select select {
        -webkit-appearance: none;
        -moz-appearance: none;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        background-color: white;
        border: 1px solid #dbdbdb;
        border-radius: 3px;
        color: #363636;
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        font-size: 14px;
        height: 28px;
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        line-height: 28px -2px;
        padding-left: 8px;
        padding-right: 8px;
        position: relative;
        vertical-align: top;
        cursor: pointer;
        display: block;
        outline: none;
        padding-right: 28px;
}
.page-options-elevator {
    display: inline-block;
    vertical-align: middle;
    height: 28px;
    line-height: 28px;
}
.page-options-elevator input {
      border-radius: 4px;
      margin: 0;
      width: 50px;
      display: inline-block;
      height: 28px;
      line-height: 1.5;
      padding: 4px 7px;
      font-size: 14px;
      border: 1px solid #ccc;
      color: #333;
      background-color: #fff;
      -webkit-transition: border 0.2s ease-in-out,background 0.2s ease-in-out,-webkit-box-shadow 0.2s ease-in-out;
      transition: border 0.2s ease-in-out,background 0.2s ease-in-out,-webkit-box-shadow 0.2s ease-in-out;
      transition: border 0.2s ease-in-out,background 0.2s ease-in-out,box-shadow 0.2s ease-in-out;
      transition: border 0.2s ease-in-out,background 0.2s ease-in-out,box-shadow 0.2s ease-in-out,-webkit-box-shadow 0.2s ease-in-out;
}
.page-options-elevator input:focus {
        border-color: #4991fd;
        outline: 0;
        -webkit-box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
                box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
}
.page-total {
  display: inline-block;
  height: 28px;
  line-height: 28px;
  margin-right: 10px;
}
.page-simple .page-next,
.page-simple .page-prev {
  margin: 0;
  border: 0;
  height: 24px;
  line-height: 24px;
  font-size: 18px;
}
.page-simple .page-simple-pager {
  display: inline-block;
  margin-right: 8px;
}
.page-simple .page-simple-pager input {
    width: 30px;
    height: 24px;
    margin: 0 8px;
    padding: 5px 8px;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-transition: border-color 0.2s ease-in-out;
    transition: border-color 0.2s ease-in-out;
}
.page-simple .page-simple-pager input:hover {
      border-color: #4991fd;
}
.page-simple .page-simple-pager span {
    padding: 0 8px 0 2px;
}
.page.mini .page-total {
  height: 20px;
  line-height: 20px;
}
.page.mini .page-item {
  margin: 0;
  min-width: 20px;
  height: 20px;
  line-height: 18px;
  border-radius: 2px;
}
.page.mini .page-options-sizer {
  line-height: 18px;
}
.page.mini .page-options-sizer-select select {
    height: 22px;
    line-height: 20px;
    border-radius: 2px;
    min-width: 20px;
    padding-right: 20px;
    font-size: 12px;
}
.page.mini .page-options-sizer-select:after {
    right: 10px;
}
.page.mini .page-next,
.page.mini .page-prev {
  margin: 0;
  min-width: 20px;
  height: 20px;
  line-height: 18px;
}
.page.mini .page-next a i:after,
  .page.mini .page-prev a i:after {
    height: 20px;
    line-height: 20px;
}
.page.mini .page-item-jump-next,
.page.mini .page-item-jump-prev {
  height: 20px;
  line-height: 20px;
  border: none;
  margin-right: 0;
}
.page.mini .page-options {
  margin-left: 8px;
}
.page.mini .page-options-elevator {
    height: 20px;
    line-height: 20px;
}
.page.mini .page-options-elevator input {
      width: 36px;
      height: 20px;
      line-height: 20px;
}



